@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
	background: linear-gradient(90deg, rgba(255,164,73,1) 0%, rgba(253,202,45,1) 100%);
}

.container{
	text-align: center;
	padding-top: 40px;
	font-size: 38px;
	color: #fff;
	font-family: 'Caveat', cursive;
}

.todoApp {
	margin: 50px;
	justify-content: flex-start;
	display: flex;
	flex-wrap: wrap;
	text-align: left;
	color: #000;
}

.dashboard-container {
  padding: 20px;
  background-color:  rgb(255, 220, 175);
	border-radius: 5px;
	margin-bottom: 20px;
	margin-left: 20px;
}

.dashboard-container:hover{
	box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.75);
}

.card-container {
	padding: 10px;
  background-color: #fff;
	border-radius: 5px;
  margin: 10px 0;
}

.card-container:hover{
	box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.75);
}

h1, h2{
	-webkit-margin-before: 0em;
	        margin-block-start: 0em;
	-webkit-margin-after: 0em;
	        margin-block-end: 0em;
}

.dashboard-name{
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	font-size: 24px;
	text-align: center;
}

.card-name{
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	font-size: 18px;
}

.card-description{
	max-width: 250px;
	font-family: 'Poppins', sans-serif;
	font-weight: 400;
	font-size: 13px;
}

.dashboardAdd{
	width: 40px;
	height: 40px;
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	font-size: 24px;
	color: #fff;
	background-color: rgb(1, 172, 72);
	border: none;
	margin-left: 30px;
	cursor: pointer;
}

.cardAdd{
	width: 20px;
	height: 20px;
	margin-left: 260px;
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	font-size: 12px;
	color: #fff;
	background-color: rgb(1, 172, 72);
	border: none;
	cursor: pointer;
}

.card-del-button{
	width: 20px;
	height: 20px;
	margin-top: 30px;
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	font-size: 12px;
	color: #fff;
	background-color: rgb(172, 27, 1);
	border: none;
	cursor: pointer;
}
